body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  margin-top: 1rem;
}

.sidebar {
  display: -webkit-flex;
  display: flex;
  margin-left: 1rem;
}

.characters {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.search-btn {
  padding: 0.5rem;
  margin-top: 0.5rem;
}

.character {
  width: 20rem;
  height: 25rem;
}

.character-image {
  height: 17rem;
  width: 17rem;
}

.search-form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  min-width: 250px;
}

.date-wrapper {
  display: block;
}

label.date-filter {
  display: inline-block;
  width: 5rem;
  text-align: left;
}

